body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}
.App-header ul {
  display:flex;
  flex-direction: row;
  justify-content: space-around;
  align-self: flex-end;
  max-width: 30vw;
  width: 100%;

}
.menu-item {
    list-style: none;
    display: flex;
    height: 100%;
    border-bottom: 2px solid #282c34;
    /* width: 100%; */

}
 .Info-wrap ul {
   padding: 0;
  list-style: none;
  display: flex;
  justify-content: space-between;
}
.short-item {
  min-height: 44vh;
  width: 45vw;
  text-align: center;
  border: 1px solid #282c34;
  border-radius: 10px;
}
.menu-item:hover {
  border-bottom: 2px solid red;
  transition: .5s ease all;
}
.menu-item a {
  font-style: normal;
  outine: none;
  text-decoration: none;
  color: #fff;

}
.App-header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  background-color: #282c34;
  max-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-header > p {
  padding-left: 20px;
}
.greeting {
  width: 100%;
  height: 85vh;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 85vh;
}
.greeting p {
  font-size: 65px;
  font-weight: 600;
  text-align: center;
}
button.arrow {
  display: block;
  background: transparent;
  outline: none;
  border:none;
  text-decoration: none;
  text-align: center;
  width: 100%;
  font-size: 30px;
}
.scroll-text{
  font-size: 30px;
  width: 100%;
  text-align: center;
}
.scroll-text > span {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
}
.Info-wrap {
  min-height: 80vh;
}
.Atom {
  color: #61dafb;
}
.App-link {
  color: #61dafb;
}
.SnakeApp {
  height: 100vh;

}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#snakeWrap {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  height: 85vh; }

.apple-info {
  width: 20px;
  height: 20px;
  display: flex;
  flex-direction: row; }

#snake {
  display: flex;
  justify-content: center;
  margin: 20px;
  width: 90vw;
  outline: none; }

.show {
  display: block; }

.hide {
  visibility: hidden; }

#gridWrap {
  position: relative; }

#grid {
  transition: all ease 2s;
  width: 570px;
  height: auto;
  /* border: 2px solid #282c34; */ }

#start, #startAgain {
  display: flex;
  align-items: center;
  text-align: center;
  height: 30px;
  transition: all ease .5s;
  outline: none;
  border: none;
  border-radius: 7px;
  background-color: #282c34;
  color: #ffffff;
  max-width: 150px;
  padding: 5px;
  margin-bottom: 10px; }

.modal {
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  /* transform: translateY(90%); */
  background-color: rgba(0, 0, 0, 0.4); }

.cell {
  width: 30px;
  height: 30px;
  /* background-color: #f6b93d; */
  background-image: url(/kobra/static/media/grass.c26f6ebc.png);
  /* background-color: green; */
  background-position: bottom;
  background-size: cover;
  /* border: 0.6px solid black; */
  display: inline-block;
  padding: 0;
  margin: -2px 0px; }

.apple {
  background-image: url(/kobra/static/media/apple.a1524b59.png);
  background-color: #529d26;
  background-position: bottom;
  background-size: cover;
  /* font-size: 20px; */ }

.apple-i {
  width: 20px;
  background-image: url(/kobra/static/media/apple.a1524b59.png);
  background-position: bottom;
  background-size: cover; }

.appleshow {
  display: flex;
  flex-direction: row;
  min-height: 20px;
  height: 100%; }

.speedBlock {
  height: 25px;
  max-width: 150px;
  width: 100%; }

.scoreBlock {
  flex-direction: column;
  width: 65%;
  display: flex;
  margin-top: 35px; }

.tail {
  background-image: url(data:image/png;base64,);
  background-color: #529d26;
  background-position: bottom;
  background-size: cover; }

.head {
  background-image: url(/kobra/static/media/snake.2a9daedd.png);
  background-color: #529d26;
  background-position: bottom;
  background-size: cover;
  background-repeat: no-repeat; }

.info {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 5vh;
  justify-content: space-evenly;
  align-items: center; }

.wraper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 90vw;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 10px; }

.scoreBlock > h3 {
  font-size: 20px;
  font-family: sans-serif;
  margin: 0; }

.speedBlock > h3 {
  cursor: pointer;
  margin: 0;
  text-align: center; }

.speedBlock > h3:last-child:hover {
  cursor: pointer; }

.mode {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  height: auto;
  min-height: 120px; }

.button_speed {
  transition: all ease .5s;
  outline: none;
  border: none;
  border-radius: 7px;
  background-color: #282c34;
  color: #ffffff;
  max-width: 150px;
  width: 100%;
  padding: 5px;
  margin-bottom: 10px; }

.button_speed:hover, #start:hover, #startAgain:hover {
  cursor: pointer;
  background-color: #1f6f8c; }

